



































import { Component, Vue } from 'vue-property-decorator';
import Swal from 'sweetalert2';
import moment from 'moment';
import { IQuestionItem, ITestCalc } from '@/types/interface';
import { State, Getter, Action } from 'vuex-class';
import banner from '../../components/Banner.vue';
import questionItem from '../../components/QuestionItem.vue';
import questionInfoPanel from '../../components/QuestionInfoPanel.vue';

interface CheckItem {
  checked: boolean;
  num: number;
}

@Component({
  components: {
    banner,
    questionItem,
    questionInfoPanel,
  },
})

export default class MainPage extends Vue {
  @State('questionList', { namespace: 'onlineTest' }) stateQuestionList!: IQuestionItem[];
  @State('keyword', { namespace: 'onlineTest' }) stateKeyword!: string;
  @Getter('courseName', { namespace: 'onlineTest' }) courseName!: string;
  @Getter('amountName', { namespace: 'onlineTest' }) amountName!: string;
  @Getter('yearName', { namespace: 'onlineTest' }) yearName!: string;
  @Getter('typeName', { namespace: 'onlineTest' }) typeName!: string;
  @Getter('levelName', { namespace: 'onlineTest' }) levelName!: string;
  @Getter('subjectName', { namespace: 'onlineTest' }) subjectName!: string;
  @Action('postCalcTest', { namespace: 'onlineTest' }) postCalcTest!: (params: ITestCalc) => Promise<void>;
  @Action('postReturnTest', { namespace: 'onlineTest' }) postReturnTest!: () => Promise<void>;

  timerSec = 0;
  intervalFunc = 0;
  select: string[][] = [];
  needCheck: boolean[] = [];

  get testInfo() {
    return {
      course: this.courseName !== '' ? this.courseName : undefined,
      amount: this.amountName !== '' ? this.amountName : undefined,
      year: this.yearName !== '' ? this.yearName : undefined,
      type: this.typeName !== '' ? this.typeName : undefined,
      level: this.levelName !== '' ? this.levelName : undefined,
      subject: this.subjectName !== '' ? this.subjectName : undefined,
      keyword: this.stateKeyword !== '' ? this.stateKeyword : undefined,
    };
  }

  get timer() {
    const hour = Math.floor(this.timerSec / 3600);
    const min = Math.floor((this.timerSec - (hour * 3600)) / 60);
    const sec = (this.timerSec - (hour * 3600) - (min * 60));
    return `${hour < 10 ? '0' : ''}${hour}:${min < 10 ? '0' : ''}${min}:${sec < 10 ? '0' : ''}${sec}`;
  }

  get answerList() {
    const temp = [];
    for (let i = 0; i < this.stateQuestionList.length; i += 1) {
      if (this.select[i] && this.select[i].length !== 0) temp.push(this.stateQuestionList[i].qNum);
    }
    return temp;
  }

  get noAnswerList() {
    const temp = [];
    for (let i = 0; i < this.stateQuestionList.length; i += 1) {
      if (this.answerList.indexOf(this.stateQuestionList[i].qNum) < 0) temp.push(this.stateQuestionList[i].qNum);
    }
    return temp;
  }

  get checkList() {
    const temp = [];
    for (let i = 0; i < this.stateQuestionList.length; i += 1) {
      if (this.needCheck[i]) temp.push(this.stateQuestionList[i].qNum);
    }
    return temp;
  }

  created() {
    this.intervalFunc = setInterval(() => {
      this.timerSec += 1;
    }, 1000);
    this.select = new Array(this.stateQuestionList.length).fill([]);
    this.needCheck = new Array(this.stateQuestionList.length).fill(false);
  }

  check(res: CheckItem) {
    const idx = this.stateQuestionList.map((d: IQuestionItem) => d.qNum).indexOf(res.num);
    Vue.set(this.needCheck, idx, res.checked);
  }

  leave() {
    Swal.fire({
      width: 288,
      showCloseButton: false,
      showCancelButton: true,
      background: '#FFFFFF',
      title: '確定離開作答',
      text: '後續可至我的測驗紀錄繼續作答',
      customClass: {
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button-half',
        cancelButton: 'modal-cancel-button-half',
      },
      confirmButtonText: '確定',
      cancelButtonText: '取消',
      reverseButtons: true,
    }).then((res) => {
      if (res.isConfirmed) this.$router.go(-1);
    });
  }

  submitCheck() {
    Swal.fire({
      width: 312,
      showCloseButton: false,
      background: '#FFFFFF',
      title: '交卷前再檢查',
      html: '<table class="font-16-normal" style="width: 100%">'
        + `<tr><td style="color: #E10101; width: 55px;">未答</td>
            <td style="width: 210px; text-align: left; padding-left: 9px; word-break: break-word;">
            ${this.noAnswerList.length === 0 ? '無' : this.noAnswerList.join(',')}</td></tr>`
        + `<tr><td style="width: 55px;">已答</td>
            <td style="width: 210px; text-align: left; padding-left: 9px; word-break: break-word;">
            ${this.answerList.length === 0 ? '無' : this.answerList.join(',')}</td></tr>`
        + `<tr><td style="width: 55px;">檢查</td>
            <td style="width: 210px; text-align: left; padding-left: 9px; word-break: break-word;">
            ${this.checkList.length === 0 ? '無' : this.checkList.join(',')}</td></tr>`
        + '</table>',
      customClass: {
        popup: 'modal-popup',
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button',
      },
      confirmButtonText: '確定',
    });
  }

  submit() {
    if (this.noAnswerList.length !== 0) return;
    Swal.fire({
      width: 312,
      showCloseButton: false,
      showCancelButton: true,
      background: '#FFFFFF',
      title: '交卷前再檢查',
      html: '<table class="font-16-normal" style="width: 100%">'
        + `<tr><td style="color: #E10101; width: 65px;">未答</td>
            <td style="width: 215px; text-align: left; padding-left: 9px; word-break: break-word;">
            ${this.noAnswerList.length === 0 ? '無' : this.noAnswerList.join(',')}</td></tr>`
        + `<tr><td style="width: 65px;">已答</td>
            <td style="width: 215px; text-align: left; padding-left: 9px; word-break: break-word;">
            ${this.answerList.length === 0 ? '無' : this.answerList.join(',')}</td></tr>`
        + `<tr><td style="width: 65px;">檢查</td>
            <td style="width: 215px; text-align: left; padding-left: 9px; word-break: break-word;">
            ${this.checkList.length === 0 ? '無' : this.checkList.join(',')}</td></tr>`
        + '</table>',
      customClass: {
        container: 'modal-container',
        popup: 'modal-popup',
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button-130',
        cancelButton: 'modal-cancel-button-130',
      },
      confirmButtonText: '送出答案',
      cancelButtonText: '繼續作答',
      reverseButtons: true,
    }).then((res) => {
      if (res.isConfirmed) {
        const list = this.stateQuestionList.map((d: IQuestionItem, idx: number) => ({
          id: d.qNum,
          // eslint-disable-next-line
          question_id: d.qId || 0,
          value: this.select[idx].join(','),
        }));

        const temp = {
          time: moment().format('YYYYMMDDHHmmss'),
          content: list,
        };

        this.postCalcTest(temp)
        .then(() => {
          Swal.fire({
            width: 288,
            showCloseButton: false,
            background: '#FFFFFF',
            title: '確認送出',
            text: '您的回答已送出，確認後觀看解答',
            customClass: {
              title: 'modal-title',
              content: 'modal-content',
              confirmButton: 'modal-confirm-button',
            },
            confirmButtonText: '確定',
          }).then((result) => {
            if (result.isConfirmed) {
              this.postReturnTest()
                .then(() => {
                  this.$router.push('/test/solution_page');
                });
            }
          });
        });
      }
    });
  }

  destroy() {
    clearInterval(this.intervalFunc);
  }
}
